// @@  LOGO 
// ////////////////////////

.logo {

  @include respond(tab-land) {
    width: 90%;
  }

  @include respond(tab-port) {
    width: 70%;
  }

  @include respond(phone) {
    width: 55%;
  }

  @include respond(smallest-phone) {
    width: 45%;
  }

  &--footer {
    width: 50%;

    @include respond(tab-land) {
      width: 70%;
    }
  
  }

  &--page {
    width: 60%;

    @include respond(tab-land) {
      width: 47%;
    }

    @include respond(phone) {
      width: 35%;
    }
  }

}